@import "https://cdnjs.cloudflare.com/ajax/libs/modern-normalize/1.1.0/modern-normalize.min.css";
@import "https://fonts.googleapis.com/css2?family=Inter:wght@500;700&family=Montserrat:wght@400;500;700&display=swap";
body {
  font-family: Montserrat, sans-serif;
}

button:hover {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0 auto;
  display: block;
}

.container {
  min-width: 270px;
  max-width: 450px;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: calc(100% - 30px);
    padding: 0;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1250px;
    padding: 0;
  }
}

.list {
  margin: 0;
  padding: 0;
}

.list__item {
  list-style-type: none;
}

.button {
  color: #fff;
  background-color: #e86f54;
  border: none;
  border-radius: 38px;
  padding: 14px 28px;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.button:hover {
  background-color: #e25b41;
}

@media screen and (min-width: 768px) {
  .button {
    font-size: 28px;
  }
}

.logo {
  fill: currentColor;
  color: #0e0e0e;
}

.form-field {
  color: #5c5c5c;
  flex-direction: column;
  row-gap: 8px;
  font-size: 14px;
  font-weight: 500;
  display: flex;
  position: relative;
}

.form-field:has(.form-field__text-input:not(:placeholder-shown):valid) {
  color: green;
}

.form-field:has(.form-field__text-input:not(:placeholder-shown):valid) .form-field__text-input {
  border: 1px solid green;
}

.form-field:has(.form-field__text-input:not(:placeholder-shown):valid) .form-field__correct-state-icon {
  fill: green;
  display: block;
}

.form-field:has(.form-field__text-input:not(:placeholder-shown):invalid) {
  color: red;
}

.form-field:has(.form-field__text-input:not(:placeholder-shown):invalid) .form-field__text-input {
  border: 1px solid red;
}

.form-field:has(.form-field__text-input:not(:placeholder-shown):invalid) .form-field__error-state-icon {
  fill: red;
  display: block;
}

.form-field__text-input {
  color: #111;
  border: 1px solid #d7d7d7;
  border-radius: 4px;
  padding: 16px;
}

.form-field__text-input::placeholder {
  color: #1116;
}

.form-field:hover {
  color: #111111b3;
}

.form-field:hover .form-field__text-input {
  border: 1px solid #111111b3;
}

.form-field__correct-state-icon, .form-field__error-state-icon {
  display: none;
  position: absolute;
  bottom: 14px;
  right: 16px;
}

.section {
  padding-bottom: 60px;
}

.section__heading {
  width: 100%;
  color: #000;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 32px;
  font-size: 24px;
  font-weight: 700;
  display: flex;
}

.section__heading:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #000;
  align-self: center;
  margin-left: 14px;
}

@media screen and (min-width: 768px) {
  .section {
    padding-bottom: 100px;
  }

  .section__heading {
    margin-bottom: 60px;
    font-size: 44px;
  }
}

.global-components, .global-components :not(h1, h3, p) {
  border: 1px solid red;
}

.header-container {
  justify-content: space-between;
  align-items: center;
  margin: 16px 0 0;
  display: flex;
}

@media screen and (min-width: 768px) {
