}

.menu-toggle {
  cursor: pointer;
  background-color: #0000;
  border: none;
  outline: none;
  margin: 0;
  padding: 0 0 0 14px;
}

@media (min-width: 768px) {
  .menu-toggle {
    display: none;
  }
}

.navbar__button--open-icon {
  width: 24px;
  height: 24px;
}

.navbar__modal {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background-color: #e86f54;
  justify-content: center;
  align-items: center;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  inset: 0;
  transform: translateX(100%);
}

.navbar__modal.is-open {
  transform: translateX(0);
}

.navbar__modal .menu-toggle {
  fill: #fff;
  position: absolute;
  top: 16px;
  right: 16px;
}

.navbar__button--close-icon {
  width: 22px;
  height: 22px;
