    margin-bottom: 60px;
    padding: 0 80px;
  }
}

.delivery__item {
  counter-increment: li-counter;
  margin-bottom: 15px;
  font-size: 14px;
  font-weight: 500;
  display: table;
}

@media screen and (min-width: 768px) {
  .delivery__item {
    margin-bottom: 20px;
