}

.delivery__item:before {
  content: counter(li-counter);
  vertical-align: middle;
  text-align: center;
  width: 40px;
  height: 40px;
  border: 1px solid #000;
  border-radius: 50px;
  font-size: 20px;
  font-weight: 500;
  line-height: 40px;
  display: inline-block;
}

@media (min-width: 1280px) {
  .delivery__item:before {
    width: 64px;
    height: 64px;
    padding-top: 10px;
    font-size: 26px;
  }
}

.slider {
  position: relative;
  overflow: hidden;
}

.slider .slider__wrapper {
  gap: 16px;
  transition: transform .5s;
  display: flex;
}

.slider .slider__wrapper.slider-action {
  animation: 20s infinite slide;
}

.slider .slider__wrapper .slider__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slider .slider__wrapper .slider__item:last-of-type {
  margin-right: 0;
}

@media (max-width: 767px) {
  .slider .slider__wrapper .slider__item {
    width: 136px;
    height: 181px;
  }
}

@media (min-width: 768px) {
  .slider .slider__wrapper .slider__item {
    width: 344px;
    height: 398px;
  }
}

@media (min-width: 1280px) {
  .slider .slider__wrapper .slider__item {
    width: 390px;
    height: 421px;
  }
}

.slider .slider__buttons {
  justify-content: center;
  gap: 25px;
  display: flex;
  position: relative;
}

.slider .slider__buttons .slider__button {
  width: 61px;
  height: 61px;
  cursor: pointer;
  background-color: #0000;
  border: none;
  display: inline-block;
}

.slider .slider__buttons .slider__button:hover {
  fill: #e25b41;
}

.slider .slider__buttons .slider__button--prev, .slider .slider__buttons .slider__button--next {
  opacity: 1;
  visibility: visible;
}

.slider:hover .slider__button--prev, .slider:hover .slider__button--next {
  opacity: 1;
  visibility: visible;
  transition: opacity .2s;
}

@keyframes slide {
  0% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(-33.33%);
  }

  50% {
    transform: translateX(-66.66%);
  }

  75% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0);
  }
}

@media (min-width: 1280px) {
  @keyframes slide {
    0% {
      transform: translateX(0);
    }

    33% {
      transform: translateX(-50%);
    }

    66% {
      transform: translateX(-100%);
    }

    100% {
      transform: translateX(0);
    }
  }
}

